.dragger {
  width: 90%;
  height: 35px;
  cursor: ns-resize;
  border-top: 1px solid #ddd;
  position: absolute;
  top: 0;
  z-index: 100;
  background-color: transparent;
  /* border: 1px solid red; */
}

.header-container-zencode {
  display: flex;
  justify-content: space-between;
  padding-left: 8px;
  padding-right: 4px;
  background-color: #e8e8e8;
  align-items: center;
  height: 30px;
}

.zencode-controls button {
  padding: .12rem .2rem;
}

/* .header-container-zencode:hover {
