.header-container {
  display: flex;
  justify-content: space-between;
  padding: 4px;
  padding-left: 8px;
  background-color: #e8e8e8;
}

.ace_editor.ace_autocomplete.ace_dark.ace-ambiance {
  width: 60%!important;
}

.ace_editor.ace_autocomplete .ace_scroller {
  width: 100%!important;
}

.header-container h6, .header-container-zencode h6 {
  font-weight: 400;
}